.checkbox {
  display: inline-block;
  flex-shrink: 0;
  width: 20px;
  height: 20px; }
  .checkbox input {
    position: absolute;
    opacity: 0;
    width: 1px;
    height: 1px; }
    .checkbox input + span {
      display: block;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      border: 1px solid #000002;
      position: relative;
      cursor: pointer; }
    .checkbox input:checked + span::before {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      background: url("../images/check.svg") center center no-repeat;
      background-size: contain;
      z-index: 1; }
    .checkbox input:disabled + span {
      cursor: default;
      border-color: #000002; }
      .checkbox input:disabled + span::before {
        display: none; }

.radio {
  display: inline-block;
  flex-shrink: 0;
  width: 20px;
  height: 20px; }
